
"use client";

import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogClose
} from "@/components/ui/dialog";
import { CastingApplication } from "@/types/casting";
import { Button } from "./ui/button";

interface CastingDetailsDialogProps {
  isOpen: boolean;
  setIsOpen: (isOpen: boolean) => void;
  application: CastingApplication | null;
}

export function CastingDetailsDialog({ isOpen, setIsOpen, application }: CastingDetailsDialogProps) {
  if (!application) return null;

  return (
    <Dialog open={isOpen} onOpenChange={setIsOpen}>
      <DialogContent className="sm:max-w-3xl">
        <DialogHeader>
          <DialogTitle className="text-primary font-headline text-3xl">
            {application.fullName}
          </DialogTitle>
          <DialogDescription>
            Detalles de la postulación
          </DialogDescription>
        </DialogHeader>
        <div className="grid gap-6 py-4 max-h-[70vh] overflow-y-auto pr-4">
            <div className="grid grid-cols-2 md:grid-cols-3 gap-x-8 gap-y-4 text-sm">
                <div>
                    <p className="font-semibold text-muted-foreground">Edad</p>
                    <p>{application.age}</p>
                </div>
                <div>
                    <p className="font-semibold text-muted-foreground">Ciudad</p>
                    <p>{application.city}</p>
                </div>
                <div>
                    <p className="font-semibold text-muted-foreground">Nacionalidad</p>
                    <p>{application.nationality || 'No especificada'}</p>
                </div>
                <div>
                    <p className="font-semibold text-muted-foreground">Teléfono</p>
                    <p className="text-primary hover:underline">
                        <a href={`tel:${application.phone}`}>{application.phone}</a>
                    </p>
                </div>
                <div>
                    <p className="font-semibold text-muted-foreground">Email</p>
                    <p className="text-primary hover:underline">
                         <a href={`mailto:${application.email}`}>{application.email}</a>
                    </p>
                </div>
                 <div>
                    <p className="font-semibold text-muted-foreground">Estatura</p>
                    <p>{application.heightCm} cm</p>
                </div>
                <div>
                    <p className="font-semibold text-muted-foreground">Talla de Ropa</p>
                    <p>{application.clothingSize}</p>
                </div>
                <div>
                    <p className="font-semibold text-muted-foreground">Talla de Calzado</p>
                    <p>{application.shoeSize || 'No especificada'}</p>
                </div>
                 <div>
                    <p className="font-semibold text-muted-foreground">Color de Ojos</p>
                    <p>{application.eyesColor || 'No especificado'}</p>
                </div>
                 <div>
                    <p className="font-semibold text-muted-foreground">Color de Cabello</p>
                    <p>{application.hairColor || 'No especificado'}</p>
                </div>
                 <div>
                    <p className="font-semibold text-muted-foreground">Confianza en Cámara</p>
                    <p>{application.cameraConfidence} / 5</p>
                </div>
            </div>
        </div>
        <div className="flex justify-end pt-4">
            <DialogClose asChild>
                <Button type="button" variant="secondary">Cerrar</Button>
            </DialogClose>
        </div>
      </DialogContent>
    </Dialog>
  );
}
