
"use client";

import { useState, useEffect } from 'react';
import { db } from '@/lib/firebase';
import { collection, getDocs, orderBy, query, DocumentData } from 'firebase/firestore';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { formatDistanceToNow } from 'date-fns';
import { es } from 'date-fns/locale';
import { Skeleton } from '@/components/ui/skeleton';
import { CastingDetailsDialog } from './casting-details-dialog';
import { CastingApplication } from '@/types/casting';
import { RowActions } from './casting-admin-row-actions';


export function CastingAdminTable() {
  const [applications, setApplications] = useState<CastingApplication[]>([]);
  const [loading, setLoading] = useState(true);
  const [selectedApplication, setSelectedApplication] = useState<CastingApplication | null>(null);
  const [isDetailsOpen, setIsDetailsOpen] = useState(false);

  useEffect(() => {
    const fetchApplications = async () => {
      try {
        const q = query(collection(db, "cast_applications"), orderBy("createdAt", "desc"));
        const querySnapshot = await getDocs(q);
        const appsData = querySnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data(),
        } as CastingApplication));
        setApplications(appsData);
      } catch (error) {
        console.error("Error fetching applications: ", error);
        // TODO: Add user-facing error message
      } finally {
        setLoading(false);
      }
    };

    fetchApplications();
  }, []);

  const handleViewDetails = (application: CastingApplication) => {
    setSelectedApplication(application);
    setIsDetailsOpen(true);
  };
  
  const getStatusVariant = (status: string) => {
    switch (status) {
      case 'received': return 'default';
      case 'in-review': return 'secondary';
      case 'shortlisted': return 'outline';
      case 'rejected': return 'destructive';
      default: return 'default';
    }
  }

  if (loading) {
    return (
      <div className="space-y-4">
        {[...Array(5)].map((_, i) => (
           <Skeleton key={i} className="h-12 w-full" />
        ))}
      </div>
    );
  }

  return (
    <>
      <div className="border rounded-lg">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead className="w-[200px]">Nombre</TableHead>
              <TableHead>Edad</TableHead>
              <TableHead>Ciudad</TableHead>
              <TableHead>Recibido</TableHead>
              <TableHead>Estado</TableHead>
              <TableHead className="text-right">Acciones</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {applications.length > 0 ? (
              applications.map((app) => (
                <TableRow key={app.id}>
                  <TableCell className="font-medium">{app.fullName}</TableCell>
                  <TableCell>{app.age}</TableCell>
                  <TableCell>{app.city}</TableCell>
                  <TableCell>
                    {app.createdAt ? formatDistanceToNow(app.createdAt.toDate(), { addSuffix: true, locale: es }) : 'N/A'}
                  </TableCell>
                  <TableCell>
                    <Badge variant={getStatusVariant(app.status)}>{app.status}</Badge>
                  </TableCell>
                  <TableCell className="text-right">
                    <RowActions
                        application={app}
                        onViewDetails={() => handleViewDetails(app)}
                    />
                  </TableCell>
                </TableRow>
              ))
            ) : (
              <TableRow>
                <TableCell colSpan={6} className="h-24 text-center">
                  No se han encontrado postulaciones.
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </div>
      {selectedApplication && (
         <CastingDetailsDialog 
            isOpen={isDetailsOpen}
            setIsOpen={setIsDetailsOpen}
            application={selectedApplication}
         />
      )}
    </>
  );
}
